<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-tianjiamujijin"></i>
				<span>新增母基金</span>
			</div>
		</div>
		<div class="form flex_between">
			<div class="form_list flex_start">
				<div class="form_list_left">
					合伙企业
				</div>
				<div class="form_list_right">
					<!-- <el-input placeholder="请输入内容" v-model="commitData.name" clearable>
					</el-input> -->
					<el-select v-model="commitData.name" :remote-method="remoteMethod" @change="changeName" :loading="loading" filterable remote :clearable='true' reserve-keyword placeholder="请输入公司名称">
						<el-option v-for="item in tianYanBackData" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
				</div>
				
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金期限（年）
				</div>
				<div class="form_list_right fund_limit_time flex_between">
					<el-input v-model="fund_end_date[0]" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="fund_end_date[1]" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="fund_end_date[2]" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金简称
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="commitData.short_name" clearable>
					</el-input>
				</div>
			</div>
			<!--<div class="form_list flex_start">
				<div class="form_list_left">
					基金到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="commitData.end_date" type="date" value-format="yyyy-MM-dd"  placeholder="选择日期">
    				</el-date-picker>
				</div>
			</div>-->
			<div class="form_list flex_start">
				<div class="form_list_left">
					备案状态
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.record_state" clearable placeholder="请选择">
					    <el-option v-for="item in allRecordState" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					协议到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="commitData.protocol_end_date" type="date" :editable='false' value-format="yyyy-MM-dd"  placeholder="选择日期">
    				</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金类型
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.is_master" clearable placeholder="请选择" >
					    <el-option v-for="item in fundTypeList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="commitData.is_master===0">
				<div class="form_list_left">
					主体母基金名称
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.p_fof_id" clearable placeholder="请选择" filterable>
					    <el-option v-for="item in masterMotherFundList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			
			
			<div class="form_list flex_start">
				<div class="form_list_left">
					普通合伙人
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.lp_user_front_id" clearable filterable placeholder="请选择">
					    <el-option v-for="item in allInvestor" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					备案编码
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="commitData.record_state_number" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					管理公司
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.sg_company_manager_id" clearable filterable placeholder="请选择">
					    <el-option v-for="item in allSgCompanyManagerList" :key="item.id" :label="item.company_name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					数据创建人
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.create_user_id" clearable  filterable placeholder="请选择">
					    <el-option v-for="item in creatorList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					币种
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.unit" clearable placeholder="请选择">
					    <el-option v-for="item in moneyTypeList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金状态
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.fund_state" clearable placeholder="请选择">
					    <el-option v-for="item in allFundState" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					工商设立日期
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="commitData.fund_date" type="date" :editable='false' value-format="yyyy-MM-dd" placeholder="选择日期" @change="toCalcYear()">
    				</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					募集状态
				</div>
				<div class="form_list_right">
					<el-select v-model="commitData.collect_state" clearable placeholder="请选择">
					    <el-option v-for="item in allCollectState" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					ODI额度(美元/万元）
				</div>
				<div class="form_list_right">
					<el-input  placeholder="请输入内容" v-model="commitData.odi" clearable>
					</el-input>
				</div>
			</div>
			
			<div class="form_list flex_start">
				<div class="form_list_left">
					是否隐藏
				</div>
				<div class="form_list_right flex_start">
					<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[commitData.is_hide?'radio_btn_active':'']">
						是
					</div>
					<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!commitData.is_hide?'radio_btn_active':'']">
						否
					</div>
				</div>
			</div>

			<div class="form_list flex_start" v-if="commitData.is_master === 0">
				<div class="form_list_left">
				</div>
				<div class="form_list_right flex_start">
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					历史倍数
				</div>
				<div class="">
					<div class="histor_multiple_box table " style="max-width:815px">
						<el-table  :data="tableData" border  >
							<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="index.toString()" :label="tableHeaderitem" width='120'>
								<template slot-scope="scope">
									<el-input  v-model="scope.row[index]" clearable @blur="historyCheck(scope.row[index],index)">
									</el-input>
								</template>
							</el-table-column>
							<el-table-column fixed="left" label="年份" width="70">
								<template slot-scope="scope">
									<span>倍数</span>
								</template>
							</el-table-column>
						</el-table>
					</div>
				</div>
			</div>
		</div>
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancleAddMotherFund()">取消</button>
			<button @click="toAddMotherFund()">保存</button>
		</div>
	</div>
</template>

<script>

import {getUserlistApi,getTianyanIdApi} from '../../config/api'
import {monterFundSelectData,addMonterFund,getParentFof} from '../../config/apiSj'
import { dialogConfirm, dialogConfirmHasbtn, isValidNumber ,isValidDecimal } from '../../config/mUtils'
	export default {
		data() {
			return {
				loading: false,
				tianYanBackData: [],

				allRecordState:[],//备案状态下拉
				allFundState:[],//基金状态
				allCollectState:[],//募集状态
				allSgCompanyManagerList:[],//所有管理公司列表
				allInvestor:[],//所有投资人列表
				creatorList:[],//创建人列表
				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				fundTypeList:[
					{
						id:1,
						name:'主体母基金'		
					},
					{
						id:0,
						name:'分支母基金'		
					},
				],//基金类型
				masterMotherFundList:[],
				fund_end_date:['','',''],
				//提交数据
				commitData:{
					name:'',
					short_name:'',
					is_master:'',//母基金类型
					p_fof_id:'',//主体母基金名称
					unit: 1,
					protocol_end_date:'',
					fund_end_date:'',
					record_state:'',
					sg_company_manager_id:'',
					lp_user_front_id:'',
					fund_date:'',
					odi:'',
					create_user_id:'',
					fund_state:'',
					collect_state:'',
					record_state_number:'',
					is_hide:1,
					tianyancha_id:'',
//					end_date:'',
				},

				//表头数据
				tableHeader:[],
				//表格数据
				tableData: [{}],

			}
		},
		created() {
			this.getMonterFundSelectData();
			this.getAllUserList();
			this.toGetParentFof();
		},
		mounted() {},
		components: {},
		methods: {

			historyCheck(multiple,index){
				if(Number(multiple)!=multiple || multiple<0){
					dialogConfirm('info','历史倍数格式错误或小于0');
					this.tableData[0][index] = 0;
				}
				if(this.tableData[0][index]==''){
					this.tableData[0][index] = 0;
				}
			},
			changeHideStatus(e){
				if (e == 1) {
					this.commitData.is_hide = 1;
				} else{
					this.commitData.is_hide = 0;
				}
			},

			//取消添加母基金
			cancleAddMotherFund(){
				this.$router.push({
					name: 'motherFundList',
				})
			},
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.fund_end_date[0]) {
					let is_valid_fund_expire1 = isValidNumber(this.fund_end_date[0]);
					let is_valid_fund_expire2 = isValidNumber(this.fund_end_date[1]);
					let is_valid_fund_expire3 = isValidNumber(this.fund_end_date[2]);
					if(this.fund_end_date[1]&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.fund_end_date[2]&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			//添加母基金
			toAddMotherFund(){
				if(this.commitData.odi!=''){
					if(!isValidDecimal(this.commitData.odi)||parseFloat(this.commitData.odi) < 0){
						dialogConfirm('info', 'ODI额度应大于等于0');
						this.commitData.odi = '';
						return
					}
				}
				
				//验证合伙企业、基金简称、管理公司比填
				if(this.commitData.name&&this.commitData.short_name&&this.commitData.sg_company_manager_id){
					//处理 基金期限（年）的数据格式
					var is_valid_number = this.isValidFundTime();
					if (is_valid_number) {
						var fund_end_date = JSON.parse(JSON.stringify(this.fund_end_date));
						if(fund_end_date[1]==''&&fund_end_date[2]==''){
							this.commitData.fund_end_date = fund_end_date[0];
						}else if(fund_end_date[1]==''&&fund_end_date[2]!=''){
							dialogConfirm("info", '基金期限不能为空!');
							return;
						}else if(fund_end_date[1]!=''&&fund_end_date[2]==''){
							fund_end_date.splice(2,1);
							this.commitData.fund_end_date = fund_end_date.join('+');
						}else{
							this.commitData.fund_end_date = fund_end_date.join('+');
						}
					} else {
						return
					}
					if(this.commitData.is_master){
						this.commitData.p_fof_id = 0;
					}
					
					let historyArr = [];
					this.tableHeader.forEach((element,index) => {
						let newObj = {
							year:element,
							multiple:this.tableData[0][index]?this.tableData[0][index]:'0',
						};
						historyArr.push(newObj);
					});
					if(historyArr.length){
						this.commitData.history = JSON.stringify(historyArr);
					}else{
						this.commitData.history = '';
					}
					addMonterFund(this.commitData).then(res => {
						if (res.data.msgNo == 1) {
							let data  = res.data.payload;
							// //console.log(res.data.payload);
							dialogConfirm('success', res.data.message);
							//跳转列表页面
							this.$router.push({
								name: 'motherFundList',
							})
						}else{
							dialogConfirm('info', res.data.message);
						}
					})
				}else{
					
					if (!this.commitData.name) {
						dialogConfirm("info", '合伙企业不能为空');
					}else if(!this.commitData.short_name){
						dialogConfirm("info", '基金简称不能为空');
					}else if(!this.commitData.sg_company_manager_id){
						dialogConfirm("info", '管理公司不能为空');
					}
				}
				
				
			},
			//获取母基金下拉数据
			getMonterFundSelectData(){
				monterFundSelectData({
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;
						// //console.log(res.data.payload);
						this.allCollectState = data.all_collect_state;
						this.allFundState = data.all_fund_state;
						this.allRecordState= data.all_record_state;
						this.allInvestor = data.all_investor;
						this.allSgCompanyManagerList = data.all_sg_company_manager_lst
					}
				})
			},
			
			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
						this.commitData.create_user_id = parseInt( window.localStorage.getItem('htcx_uid')) ;
					}
				})
			},

			//获取所有的主体母基金
			toGetParentFof(){
				getParentFof().then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						//console.log(res);
						this.masterMotherFundList = data;
					}
				})
			},

			changeName(e){
				// //console.log(e);
				this.getTianYanId()
			},
			getTianYanId(){
				var data = this.tianYanBackData;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].name == this.commitData.name) {
						if (data[i].id) {
							this.commitData.tianyancha_id = data[i].id;
						} else{
							this.commitData.tianyancha_id = '';
						}
						return
					}
				}
			},
			// 获取天眼查返回的数据
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getTianyanIdApi({
						name: query
					}).then(res => {
						this.loading = false; 
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.tianYanBackData = [{
									name: query
								}]
							} else {
								let newObj = {
									name: query
								}
								res.data.payload.unshift(newObj);
								this.tianYanBackData = res.data.payload;
							}

						}
					})
				} else {
					this.tianYanBackData = [];
				}
			},
			
			//计算出来对应的历史倍数年限
			toCalcYear(){
				this.tableHeader = [];
				this.tableData = [{}];
				if(this.commitData.fund_date&&this.commitData.protocol_end_date){
					let  fund_date = Date.parse(this.commitData.fund_date);//工商设置日期
					let  end_date = Date.parse(this.commitData.protocol_end_date);//基金到期日
					if(fund_date <= end_date){
						let fund_year = this.commitData.fund_date.split('-')[0];
						let end_year = this.commitData.protocol_end_date.split('-')[0];
						let number = parseInt(end_year) - parseInt(fund_year) ;
						for(let i = 0;i<=number;i++){
							this.tableHeader.push((parseInt(fund_year)+i).toString());
						}
					}else{
						this.commitData.fund_date = '';
						dialogConfirm('info','工商设立日期不能大于基金到期日');
					}
				}
				
				if(this.tableHeader.length){
					let tableDataObj = [{}];
					this.tableHeader.forEach((element,index) => {
						tableDataObj[0][index] = 0
						
					});
					this.tableData=tableDataObj;
				}
				
			}
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form{
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 19px;
	}
	.form_list_left{
		width: 110px;
		color: #6F7071;
	}
	.form_list_right{
		width: 290px;
    		height: 40px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
</style>


